<script>


export default {
  name:'HeaderVue',
  components: { 

  },
  data() {
    return{
        // scrollPosition: null

        showNavbar: true,
        lastScrollPosition: 0,
    }
  },
   methods: {
    // updateScroll(){
    //     this.scrollPosition = window.scrollY
    //  }

    onScroll() {
      // Get the current scroll position
      const currentScrollPostion = window.pageYOffset || document.documentElement.scrollTop
      // Because of momentum scrolling on mobiles, we shouldn't continue if it is less than zero
      if (currentScrollPostion < 0) {
        return
      }
      // Stop executing this function if the difference between
      // current scroll position and last scroll position is less than some offset
      if (Math.abs(currentScrollPostion - this.lastScrollPosition) < 70) {
        return
      }
      // Here we determine whether we need to show or hide the navbar
      this.showNavbar = currentScrollPostion < this.lastScrollPosition
      // Set the current scroll position as the last scroll position
      this.lastScrollPosition = currentScrollPostion
    }
    },
   mounted(){
        // window.addEventListener('scroll', this.updateScroll);

        window.addEventListener('scroll', this.onScroll)
    },
    beforeDestroy() {
      window.removeEventListener('scroll', this.onScroll)
    }
  
}
</script>

